<template>
  <div>
    <div>
      <div style="float:left;">
        <el-input style="width: 500px;" prefix-icon="el-icon-search" type="text" auto-complete="false" v-model="table.search" placeholder="()" @blur="searchUser"></el-input>
      </div>
      <div style="float:right">
        <el-button type="primary" @click="dialogFormVisibleAdd=true"></el-button>
        <el-button type="danger" @click="deleteUser()"></el-button>
      </div>
    </div>
    <template>
      <el-table ref="singleTable"
                :data="this.table.tableData"
                highlight-current-row
                style="width: 100%"
                @selection-change="handleSelectionChange">
        <el-table-column property="userId" v-if="false"></el-table-column>
        <el-table-column type="selection"></el-table-column>
        <el-table-column type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.username }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.email }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="form.updateUserId = scope.row.userId;
                                              form.username = scope.row.username;
                                              form.email = scope.row.email;
                                              dialogFormVisible = true"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 30px">
        <el-pagination
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          :current-page="this.table.currentPage"
          :page-sizes="[5, 7, 10, 15, 20]"
          :page-size=this.table.pageSize
          layout="total, sizes, prev, pager, next, jumper"
          :total=this.table.total>
        </el-pagination>
      </div>
    </template>
    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="" prop="username" :label-width="formLabelWidth">
          <el-input v-model="form.username" type="text" auto-complete="false" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" :label-width="formLabelWidth">
          <el-input v-model="form.password" type="password" auto-complete="false" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="email" :label-width="formLabelWidth">
          <el-input v-model="form.email" type="text" auto-complete="false" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="modifyUser"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="addForm" :rules="rules" ref="addForm">
        <el-form-item label="" prop="username" :label-width="formLabelWidth">
          <el-input v-model="addForm.username" type="text" auto-complete="false" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" :label-width="formLabelWidth">
          <el-input v-model="addForm.password" type="password" auto-complete="false" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="email" :label-width="formLabelWidth">
          <el-input v-model="addForm.email" type="text" auto-complete="false" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from 'element-ui'
const querystring = require('querystring')

export default {
  name: 'UserManage',

  created () {
    // 
    this.getRequest('/user', this.table).then(resp => {
      this.table.total = resp.obj.total
      this.table.tableData = resp.obj.records
    })
  },
  data () {
    return {
      dialogFormVisible: false,
      dialogFormVisibleAdd: false,
      formLabelWidth: '70px',
      table: {
        tableData: [],
        currentPage: 1,
        pageSize: 7,
        total: 0,
        search: '',
        multipleSelection: []
      },
      form: {
        username: '',
        password: '',
        email: '',
        updateUserId: 0
      },
      addForm: {
        username: '',
        password: '',
        email: ''
      },
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    handleCurrentChange (val) {
      this.table.currentPage = val
      this.getRequest('/user', this.table).then(resp => {
        this.table.tableData = resp.obj.records
      })
    },
    // 
    handleSizeChange (val) {
      this.table.pageSize = val
      this.getRequest('/user', this.table).then(resp => {
        this.table.tableData = resp.obj.records
      })
    },
    // 
    handleSelectionChange (val) {
      this.table.multipleSelection = []
      val.forEach((item) => {
        this.table.multipleSelection.push(item.userId)
      })
    },
    // 
    searchUser () {
      this.getRequest('/user', this.table).then(resp => {
        this.table.total = resp.obj.total
        this.table.tableData = resp.obj.records
      })
    },
    // 
    addUser () {
      this.$refs.addForm.validate(async (valid) => {
        if (valid) {
          await this.postRequest('/user', querystring.stringify(this.addForm))
          await this.getRequest('/user', this.table).then(resp => {
            this.table.total = resp.obj.total
            this.table.tableData = resp.obj.records
            this.addForm.username = ''
            this.addForm.password = ''
            this.addForm.email = ''
          })
          this.dialogFormVisibleAdd = false
        } else {
          this.$message.error('')
          return false
        }
      })
    },
    // 
    modifyUser () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          await this.putRequest('/user/' + this.form.updateUserId, this.form)
          await this.getRequest('/user', this.table).then(resp => {
            this.table.total = resp.obj.total
            this.table.tableData = resp.obj.records
            this.form.username = ''
            this.form.password = ''
            this.form.email = ''
          })
          this.dialogFormVisible = false
        } else {
          this.$message.error('')
          return false
        }
      })
    },
    // 
    deleteUser () {
      if (Array.from(this.table.multipleSelection).length === 0) {
        Message.error({ message: '' })
        return
      }
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.deleteRequest('/user', this.table)
        await this.getRequest('/user', this.table).then(resp => {
          this.table.total = resp.obj.total
          this.table.tableData = resp.obj.records
          // 
          if (this.table.total % this.table.pageSize === 0 && this.table.total !== 0) {
            this.table.currentPage--
            this.getRequest('/user', this.table).then(resp => {
              this.table.tableData = resp.obj.records
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>
<style scoped>
.block {
  height: 50px;
}
</style>
